body {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 320px; }

img {
  max-width: 100%;
  height: auto;
  object-fit: cover; }

.visually-hidden:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 116px;
  width: 320px;
  min-height: 600px;
  padding-top: 11px;
  background: url(../../images/dest/header/background-mobile.jpg) top no-repeat #c3726a;
  background-size: cover; }

.header__container {
  width: 290px;
  margin: 0 auto;
  /*@media (min-width: $tablet-width) {
    width: $tablet-content-width;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-content-width;
  }*/ }

.header__top-panel {
  margin-bottom: 11px; }

.header__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  margin-bottom: 30px; }
  .header__title .header__title-first {
    height: 99px;
    margin-left: 5px;
    font-family: "Besom", "Arial", sans-serif;
    font-size: 102px;
    letter-spacing: 4.5px;
    text-shadow: -0.279px 3.99px 7.92px rgba(0, 0, 0, 0.34);
    color: #ff0000; }
  .header__title .header__title-second {
    margin-left: 5px;
    font-family: "Core-95-Black", "Arial", sans-serif;
    font-size: 38px;
    text-transform: uppercase;
    letter-spacing: 1.9px;
    white-space: nowrap;
    color: #ffffff; }

.top-panel {
  display: grid;
  justify-content: space-between;
  align-content: space-between;
  min-height: 124px; }

.top-panel__picture {
  grid-column: 1/2;
  grid-row: 1/2;
  width: 58px;
  height: 58px; }

.top-panel__logo {
  width: 58px;
  height: 58px; }

.top-panel__tel {
  grid-column: 2/3;
  grid-row: 1/2;
  height: 52px;
  font-family: "Core-55-Medium", "Arial", sans-serif;
  font-size: 18px;
  line-height: 52px;
  text-decoration: none;
  color: #ffffff; }

.top-panel__title {
  grid-column: 1/3;
  grid-row: 2/3;
  margin: 0;
  padding: 0;
  font-family: "Core-45-Regular", "Arial", sans-serif;
  font-size: 16px;
  letter-spacing: 11.2px;
  text-transform: lowercase;
  color: #ffffff; }

.short-plan {
  display: flex;
  align-items: center;
  min-height: 47px;
  margin: 0;
  padding: 0;
  padding-left: 12px;
  font-family: "Core-95-Black", "Arial", sans-serif;
  font-size: 14.4px;
  text-transform: uppercase;
  color: #1b1916;
  list-style: none;
  background: url(../../images/dest/header/short-plan/background-mobile.png) no-repeat;
  background-size: 205px 40px;
  background-position: left center; }

.short-plan__item:nth-child(1) {
  margin-right: 64px; }

.short-plan__item:nth-child(2) {
  margin-right: 62px; }

.short-plan__item:nth-child(3) {
  margin-right: 25px; }

.short-plan__item:nth-child(4) {
  flex-shrink: 0;
  width: 77px;
  margin-top: 5px;
  font-family: "Core-35-LightIt", "Arial", sans-serif;
  font-size: 14px;
  text-transform: none;
  color: #ffffff; }

.risk-btn {
  display: block;
  width: 307px;
  height: 73px;
  padding-left: 107px;
  background: url(../../images/dest/risk-btn/background-mobile.png) no-repeat;
  background-size: 307px 73px;
  background-position: center;
  font-family: "Core-95-Black", "Arial", sans-serif;
  font-size: 18px;
  line-height: 60px;
  letter-spacing: 0.9px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  transition-property: background;
  transition-duration: 150ms; }

.risk-btn:hover {
  background: url(../../images/dest/risk-btn/background-hover-mobile.png) no-repeat; }

.risk-btn:active {
  padding-top: 1px;
  background: url(../../images/dest/risk-btn/background-active-mobile.png) no-repeat; }

