.grid {
  margin: 0 auto; }

.grid::before {
  content: "";
  position: fixed;
  top: 0;
  bottom: 0;
  margin-left: -15px;
  width: 320px;
  pointer-events: none;
  background-image: repeating-linear-gradient(to right, rgba(0, 100, 0, 0.2) 0, rgba(0, 100, 0, 0.2) 15px, transparent 15px, transparent 38px, rgba(0, 100, 0, 0.2) 38px, rgba(0, 100, 0, 0.2) 53px); }

