@mixin container {
  width: $mobile-content-width;
  margin: 0 auto;

  /*@media (min-width: $tablet-width) {
    width: $tablet-content-width;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-content-width;
  }*/
}

@mixin resetSpaces {
  margin: 0;
  padding: 0;
}

@mixin column {
  display: flex;
  flex-direction: column;
  align-items: center;
}
